.login-wrapper {
  margin-top: 0; }
  .login-wrapper p {
    margin-bottom: 1.5rem !important; }

.or {
  margin-top: .5rem !important;
  padding: 5px; }

.line {
  width: 42.5% !important;
  text-align: left; }

.img-responsive {
  margin-top: -10rem !important; }

.login-container {
  margin-left: 25rem;
  margin-top: -15rem !important; }

.signup-container {
  text-align: left; }
  .signup-container .flex-left {
    padding-right: 2%; }
  .signup-container input[type='text'] {
    color: #678;
    width: 100%;
    margin-bottom: 15px !important; }
  .signup-container input[type='email'] {
    color: #678;
    width: 100%;
    margin-bottom: 15px !important; }
  .signup-container input[type='password'] {
    color: #678;
    width: 100%;
    margin-bottom: 0px !important; }
  .signup-container .side_input {
    width: 100%;
    display: flex; }
  .signup-container .form-button {
    width: 100%; }
  .signup-container a {
    color: #FFFFFF !important; }
  .signup-container .center {
    text-align: center; }
  .signup-container .center a {
    text-decoration: underline; }
  .signup-container .inValid, .signup-container .inValid:focus {
    border: 1px solid red;
    box-shadow: none; }
  .signup-container .small-text {
    font-size: 10px;
    margin-top: 0px !important;
    font-style: italic;
    color: #ffff4d;
    text-align: center; }
  .signup-container .form-button {
    margin-top: 5px !important; }
  .signup-container .text-center {
    text-align: center; }
  .signup-container .mobile-text {
    display: none; }

@media (min-width: 10em) and (max-width: 43em) {
  body {
    overflow: auto !important; }
  .signup-container {
    text-align: left;
    width: 60%;
    margin: 0 auto; }
  .login-wrapper {
    padding-top: 0 !important; } }

@media (min-width: 10em) and (max-width: 59em) {
  body {
    overflow: auto !important; }
  .signup-container {
    text-align: left;
    width: 60%;
    margin: 0 auto; }
    .signup-container input[type=password] {
      margin-bottom: 15px !important; }
  .side_input {
    display: block !important; }
  .small-text {
    color: red !important; } }

@media (min-width: 1024px) and (max-width: 1336px) {
  .side_input {
    display: block !important; }
  .small-text {
    color: red !important; }
  .input-group {
    margin-bottom: 15px !important; } }
